<script>
import Header from '@/components/header/Header.vue'
import Footer from '@/components/footer/Footer.vue'
import AlertsContainer from '@/components/partial/AlertsContainer'
import Background from '@/components/views/exchange/Background.vue'

// import anime from 'animejs'

export default {
  name: 'About',
  components: {
    AlertsContainer,
    Header,
    Footer,
    Background,
  },
  data: () => ({
    scrollPermit: true,
    waypointOptions: {
        root: null,
        rootMargin: '0px 0px 0px 0px',
        threshold: [0, 0.5],
      },
    years: [
      {
        year: 'Jan 2015',
        desc: `Cloud mining services suspended.`,
        selected: true,
        opacity: 1,
      },
      {
        year: 'Mar 2015',
        desc: `MSB status in FinCEN obtained.`,
        selected: false,
        opacity: 0.1,
      },
      {
        year: 'Apr 2015',
        desc: `Simple Buy/Sell service launched.`,
        selected: false,
        opacity: 0,
      },
      {
        year: 'Oct 2015',
        selected: false,
        desc: `Integration with TradingView platform.`,
        opacity: 0,
      },
      {
        year: 'Dec 2015',
        desc: `Mobile application released.`,
        selected: false,
        opacity: 0,
      },
      {
        year: 'Apr 2016',
        desc: `ETH market added to N. Withdrawals to cards launched.`,
        selected: false,
        opacity: 0,
      },
      {
        year: 'Dec 2017',
        desc: `Maker Taker Fee Schedule Launched`,
        selected: false,
        opacity: 0,
      },
      {
        year: 'Jan 2017',
        desc: `PCI DSS Certificate Upgraded to Level 2`,
        selected: false,
        opacity: 0,
      },
      {
        year: 'Oct 2017',
        desc: `Variable Trade Fee Schedule Launched`,
        selected: false,
        opacity: 0,
      },
      {
        year: 'Mar 2018',
        desc: `Margin Trading Launched`,
        selected: false,
        opacity: 0,
      },
      {
        year: 'Jun 2018',
        desc: `Bitcoin Affiliate Program Launched`,
        selected: false,
        opacity: 0,
      },
    ],
  }),
  computed: {
    computedYears() {
      return this.years
    },
  },
  methods: {
    onWaypoint({ going }) {
      const services = document.querySelectorAll('.service');
      if (going === this.$waypointMap.GOING_IN) {
        let time = 0;
        services.forEach((advantage) => {
          setTimeout(() => {
            advantage.classList.add('animated', 'fadeInUp', 'visible');
          }, time);
          time += 100;
        });
      }
    },
    go(direction) {
      for (let i = 0; i < this.years.length; i++) {
        if (this.years[i].selected) {
          this.years[i].selected = false

          var next =
            i + direction < 0
              ? this.years.length - 1
              : i + direction > this.years.length - 1
              ? 0
              : i + direction

          this.years[next].selected = true

          break
        } else {
          continue
        }
      }

      this.updateOpacity()
      this.updatePosition()
    },
    updateYearsPosition() {
      var start = 16
      var step = 8
      for (let i = 0; i < this.years.length; i++) {
        this.years[i].position = start + 'rem'
        start -= step
      }
    },
    updatePosition() {
      for (let i = 0; i < this.years.length; i++) {
        if (this.years[i].selected) {
          this.$el.querySelector('.overview-foreground').style.top = this.years[
            i
          ].position
          break
        } else {
          continue
        }
      }
    },
    updateOpacity() {
      for (let i = 0; i < this.years.length; i++) {
        if (this.years[i].selected) {
          this.years[i].opacity = 1
        }

        if (this.years[i - 1]?.selected || this.years[i + 1]?.selected) {
          this.years[i].opacity = 0.1
        }

        if (
          !this.years[i].selected &&
          !this.years[i - 1]?.selected &&
          !this.years[i + 1]?.selected
        ) {
          this.years[i].opacity = 0
        }
      }
    },
  },
  mounted() {
    this.updateYearsPosition()
    document.body.style.overflowY = 'auto'

    let self = this

    setInterval(() => {
      self.go(1)
    }, 1500)
  },
}
</script>

